let BaCayEvent = {};
BaCayEvent.RESPONSE_NAME = {
    SIT_ON_RES : "bc1",
    SIT_OUT_RES : "bc2",
    STAND_UP_RES : "bc3",
    PRE_START_RES : "bc4",
    START_RES : "bc5",
    BET_RES : "bc6",
    KICK_RES : "bc7",
    FINISH_GAME_RES : "bc8",
    LOAD_TABLE_RES : "bc9",
    CAN_REGISTER_OWNER_RES : "bc10",
    BET_GA_RES : "bc11",
    REGISTER_OWNER_RES: "bc12",
    SELL_OWNER_RES: "bc13"
};
BaCayEvent.UserSitOnEvent = CasinoEvent._BaseEvent.extend({
    ctor(){
        this._super(BaCayEvent.RESPONSE_NAME.SIT_ON_RES);
    },
    fromEvent(event){
        let param = event.get(BaCayEvent.RESPONSE_NAME.SIT_ON_RES);
        this.m_deskId = param.getInt("did");
        this.m_userName = param.getUtfString("u");
        this.m_currentUser = param.getUtfString("cu");
        this.m_deskState = param.getUtfString("ds");
        this.m_buyIn = param.getDouble("c");
        return this;
    }
});
BaCayEvent.UserSitOutEvent = CasinoEvent._BaseEvent.extend({
    ctor(){
        this._super(BaCayEvent.RESPONSE_NAME.SIT_OUT_RES);
    },
    fromEvent(event){
        let param = event.get(BaCayEvent.RESPONSE_NAME.SIT_OUT_RES);
        this.m_username = param.getUtfString("u");
        this.m_isSitOut = param.getBool("io");
        return this;
    }
});
BaCayEvent.UserStandUp = CasinoEvent._BaseEvent.extend({
    ctor(){
        this._super(BaCayEvent.RESPONSE_NAME.STAND_UP_RES);
    },
    fromEvent(event){
        let param = event.get(BaCayEvent.RESPONSE_NAME.STAND_UP_RES);
        this.m_username = param.getUtfString("u");
        return this;
    }
});
BaCayEvent.PreStartEvent = CasinoEvent._BaseEvent.extend({
    ctor(){
        this._super(BaCayEvent.RESPONSE_NAME.PRE_START_RES);
    },
    fromEvent(event){
        this.m_isPrestart = event.getBool("ip");
        this.m_prestartTime = event.getInt("t");
        return this;
    }
});
BaCayEvent.StartEvent = CasinoEvent._BaseEvent.extend({
    ctor(){
        this._super(BaCayEvent.RESPONSE_NAME.START_RES);
    },
    fromEvent(event){
        this.m_owner = event.getUtfString("u");
        this.m_gameTime = event.getInt("t");
        this.m_listActiveUserName = event.getUtfStringArray("lu");
        return this;
    }
});
BaCayEvent.BetEvent = CasinoEvent._BaseEvent.extend({
    ctor(){
        this._super(BaCayEvent.RESPONSE_NAME.BET_RES);
    },
    fromEvent(event){
        this.err = event.containsKey("ec") ? event.getUtfString("ec") : undefined;
        if(!this.err){
            this.m_username = event.getUtfString("u");
            this.m_bet = event.getDouble("b");
            return {bet : this.m_bet, username: this.m_username, error: null};
        }
        return {bet : this.m_bet, username: this.m_username, error: this.err};
    }
});
BaCayEvent.BetGaEvent = CasinoEvent._BaseEvent.extend({
    ctor(){
        this._super(BaCayEvent.RESPONSE_NAME.BET_GA_RES);
    },
    fromEvent(event){
        this.err = event.containsKey("ec") ? event.getUtfString("ec") : undefined;
        if(!this.err){
            this.m_username = event.getUtfString("u");
            this.m_bet = event.getDouble("p");
            return {bet: this.m_bet, username: this.m_username, error: null};
        }
        return {bet: null, username: this.m_username, error: this.err};
    }
});
BaCayEvent.KickUserEvent = CasinoEvent._BaseEvent.extend({
    ctor(){
        this._super(BaCayEvent.RESPONSE_NAME.KICK_RES);
    },
    fromEvent(event){
        let param = event.get(BaCayEvent.RESPONSE_NAME.KICK_RES);
        this.m_resType = param.getInt("rt");
        this.m_username = param.getUtfString("u");
        return this;
    }
});
BaCayEvent.FinishGameEvent = CasinoEvent._BaseEvent.extend({
    ctor(){
        this._super(BaCayEvent.RESPONSE_NAME.FINISH_GAME_RES);
        this.m_listActiveUserName = [];
        this.m_listChipWin = [];
        this.m_listPoint = [];
        this.m_listHandCard = [];
        this.m_winGa = null;
    },
    fromEvent(event){
        this.m_listActiveUserName = event.getUtfStringArray("lu");
        this.m_listChipWin        = event.getDoubleArray("lw");
        this.m_listPoint          = event.getIntArray("lp");
        this.m_winGa              = event.getUtfString("wg");
        let sfsListHandCard = event.getSArray("lc");
        for(let i = 0; i < sfsListHandCard.size();i++){
            let sfsHandCards = sfsListHandCard.get(i).getObject();
            let cards = [];
            for(let j = 0; j < sfsHandCards.size();j++){
                cards.push(sfsHandCards.get(j).getObject());
            }
            this.m_listHandCard[i] = cards;
        }
        return this;
    }
});
BaCayEvent.LoadTableInfoEvent = CasinoEvent._BaseEvent.extend({
    ctor(){
        this._super(BaCayEvent.RESPONSE_NAME.LOAD_TABLE_RES);
        this.m_isGameStart = false;
        this.m_isPrestart = false;
        this.m_prestartTime = 0;
        this.m_listDesk = [];
        this.m_listUserSitOut = [];
        this.m_listUser = [];
        this.m_listActiveUserName = [];
        this.m_listHandCard = [];
        this.m_username = null;
    },
    fromEvent(event){
        let param = event.get(BaCayEvent.RESPONSE_NAME.LOAD_TABLE_RES);
        this.m_isGameStart = param.getBool("is");
        this.m_isPrestart = param.getBool("ip");
        this.m_prestartTime = param.getInt("pt");
        this.m_username = param.getUtfString("ow");
        let sfsListDesk = param.getSArray("ld");
        for(let i = 0; i < sfsListDesk.size(); i++){
            let desk = this.deskFromSObject(sfsListDesk.get(i).getObject());
            this.m_listDesk.push(desk);
        }

        this.m_listUserSitOut = param.getUtfStringArray("luo");
        this.m_listUser = param.getUtfStringArray("lu");
        this.m_listActiveUserName = param.getUtfStringArray("lau");

        let sfsListHandCard = param.getSArray("lc");
        for(let i = 0; i < sfsListHandCard.size();i++){
            let sfsHandCards = sfsListHandCard.get(i).getObject();
            let cards = [];
            for(let j = 0; j < sfsHandCards.size();j++){
                cards.push(sfsHandCards.get(j).getObject());
            }
            this.m_listHandCard[i] = cards;
        }
        return this;
    },
    deskFromSObject(obj){
        let desk = {};
        desk.DeskId = obj.getInt("id");
        desk.UserName = obj.getUtfString("u");
        desk.DeskState = obj.getUtfString("s");
        desk.UserChip = obj.getDouble("c");
        return desk;
    }
});
BaCayEvent.CanRegisterOwnerEvent = CasinoEvent._BaseEvent.extend({
    ctor(){
        this._super(BaCayEvent.RESPONSE_NAME.CAN_REGISTER_OWNER_RES);
    },
    fromEvent(event){
        this.m_listUser = event.getUtfStringArray("lu");
        return this;
    }
})
BaCayEvent.RegisterOwnerEvent = CasinoEvent._BaseEvent.extend({
    ctor(){
        this._super(BaCayEvent.RESPONSE_NAME.REGISTER_OWNER_RES);
        this.err = "";
        this.m_username = "";
    },
    fromEvent(event){
        this.m_username = event.containsKey("u") ? event.getUtfString("u") : "";
        this.err = event.containsKey("ec") ? event.getUtfString("ec") : "";
        return this;
    }
});
BaCayEvent.SellOwnerEvent = CasinoEvent._BaseEvent.extend({
    ctor(){
        this._super(BaCayEvent.RESPONSE_NAME.SELL_OWNER_RES);
        this.err = "";
        this.m_username = "";
    },
    fromEvent(event){
        this.m_username = event.containsKey("u") ? event.getUtfString("u") : "";
        this.err = event.containsKey("ec") ? event.getUtfString("ec") : "";
        return this;
    }
});
window.BaCayEvent = module.exports = BaCayEvent;
